import { makeStyles } from "@material-ui/styles"

export default makeStyles(theme => ({
    root: {
        ...theme.typography.body1,
        position: "absolute",
        width: "100%",
        height: "100%",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
    },
    weatherInfo: {
        flex: 1,
        width: "100%",
    },
    citySearchContainer: {
        position: "relative",
        margin: theme.spacing(4),
        width: "30%",
    },
    citySearch: {
        margin: 0,
    },
    outline: {
        borderRadius: theme.spacing(1.5),
    }
}))