import React from "react"
import useLogic from "./useLogic"
import { TextField } from "@material-ui/core"
import Autocomplete from "components/Autocomplete"
import LoadingBar from "components/LoadingBar"
import CurrentWeather from "components/CurrentWeather"

const Main = props => {
    const { classes, cityName, onChange, onSelect, inputRef, onFocus, onBlur, showDialog } = useLogic()

    return (
        <div className={classes.root} onClick={onBlur}>
            <LoadingBar />
            <div className={classes.weatherInfo}>
                <CurrentWeather />
            </div>

            <div className={classes.citySearchContainer}>
                <Autocomplete
                    show={showDialog}
                    onSelect={onSelect}
                    cityName={cityName} />
                <TextField
                    onFocus={onFocus}
                    inputRef={inputRef}
                    fullWidth
                    className={classes.citySearch}
                    value={cityName}
                    onChange={onChange}
                    InputProps={{ classes: { notchedOutline: classes.outline } }}
                    placeholder="search city..."
                    margin="dense"
                    variant="outlined" />
            </div>
        </div>
    )
}

export default Main